import Switch from 'component/Switch';
import { useState } from 'react';
import styles from './index.less'

const SwitchLoadingView = () => {
    const [flag, setFlag] = useState(false);
    const [loading, setLoading] = useState(false)
    return (

        <Switch
            checked={flag}
            loading={loading}
            onChange={(checked) => {
                setLoading(true);
                setTimeout(() => {
                    setFlag(checked);
                    setLoading(false)
                }, 1000);
            }} />

    )
}

export default SwitchLoadingView;