export default "import Switch from 'component/Switch';\nimport { useState } from 'react';\nimport styles from './index.less'\n\nconst SwitchLoadingView = () => {\n    const [flag, setFlag] = useState(false);\n    const [loading, setLoading] = useState(false)\n    return (\n\n        <Switch\n            checked={flag}\n            loading={loading}\n            onChange={(checked) => {\n                setLoading(true);\n                setTimeout(() => {\n                    setFlag(checked);\n                    setLoading(false)\n                }, 1000);\n            }} />\n\n    )\n}\n\nexport default SwitchLoadingView;";