export default "import Switch from 'component/Switch';\nimport { useState } from 'react';\nimport styles from './index.less'\n\nconst SwitchBaseView = () => {\n    const [flag, setFlag] = useState(false)\n    return (\n        <div className={styles.container}>\n            <div className={styles.item}>\n                <span>默认： </span> <Switch checked={flag} onChange={(type) => { setFlag(type) }} />\n            </div>\n\n            <span>禁用的：</span> <Switch disabled />\n        </div>\n    )\n}\n\nexport default SwitchBaseView;";